<template>
  <div id="app">

    <ul class="nav nav-pills nav-stacked">
      <li>&nbsp;</li>

      <li class="text-center ">
        <br><br>
        <span class="glyphicon glyphicon-fire logo"></span>
        <br>
        <br>

        <br>
        <br>
      </li>
      <li class="line"></li>
      <li class="border-bottom text-center"  v-on:click.prevent="changecomponent('stu_message')"><span class="glyphicon glyphicon-user">&nbsp;<a class="border-bottom">个人信息</a></span></li>
      <li class="line"></li>
      <li class="border-bottom text-center"  v-on:click.prevent="changecomponent('course_message')"><span class="glyphicon glyphicon-th-list">&nbsp;<a class="border-bottom">课程信息</a></span></li>
      <li class="line"></li>
      <li class="border-bottom text-center"  v-on:click.prevent="changecomponent('select_course')"><span class="glyphicon glyphicon-ok">&nbsp;<a class="border-bottom">选课系统</a></span></li>


    </ul>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "student_menu",
    data(){
        return {
            course_list:[]
        }
    },
    methods:{
        changecomponent(componentname){
            var usermessage = {username:this.$store.state.username}
            //alert("点击："+componentname+"学号："+usermessage.username);
            if(componentname == 'stu_message') {
                axios.post("/api/stu_message", usermessage).then((res) => {
                    // var property = "";
                    // for (var item in res.data) {
                    //     property += "属性：" + item + "数值：" + (res.data)[item] + "\n";
                    // }
                    // console.log("property:"+property)

                    //console.log("获取信息情况" + res.data.toString());
                    this.$emit('getshowcomponent', componentname, res.data);
                }).catch(err => {
                    console.log("获取个人信息失败");
                });
            }else if(componentname == 'course_message'){
               // alert("当前执行course_message,学生："+this.$store.state.username)
                axios.get("/api/getSelectedCourse",{params:{
                    stu_id:this.$store.state.username
                    }}).then((res) => {
                    console.log("获取信息情况" + res.data.toString());
                    this.$emit('getshowcomponent', componentname,res.data);
                }).catch(err => {
                    console.log("获取课程信息失败")
                });
            }else if(componentname == 'select_course'){
                this.course_list = [];
                axios.get("/api/getSelectedCourse",{params:{
                        stu_id:this.$store.state.username
                    }}).then((res) => {
                     //console.log("获取信息情况" + res.data.toString());
                    for(var item in res.data){
                        this.course_list.push((res.data)[item].id);
                    }
                    //this.$emit('getshowcomponent', componentname,res.data);
                    //console.log(this.course_list);
                    this.$emit('getcourse_list',this.course_list);
                }).catch(err => {
                    console.log("获取课程信息失败")
                });

                axios.get('/api/getCourse').then((res) => {
                   // console.log("获取选课信息情况："+res.data.toString());
                    this.$emit("getshowcomponent",componentname,res.data);
                }).catch(err => {
                    console.log("获取选课信息失败");
                });
            }
        }
    },

  }
</script>

<style scoped>
    .logo{
      font-size: 60px;

    }
    .line{
      border-bottom: 1px solid;
    }
    .border-bottom:hover{
      background-color: slateblue;
    }
    .border-bottom{
      padding-top: 10px;
      height:40px;

    }
    a{
      color:#fff;
      font-size: large;
    }
  #app{
    margin-left:10%;
    margin-right: 10%;
    background-color: darkslateblue;
    color:white;
  }


</style>



