<template>
  <div id="app">

    <ul class="nav nav-pills nav-stacked">
      <li>&nbsp;</li>

      <li class="text-center ">
        <br><br>
        <span class="glyphicon glyphicon-fire logo"></span>
        <br>
        <br>

        <br>
        <br>
      </li>
      <li class="line"></li>
      <li class="border-bottom text-center"  v-on:click.prevent="changecomponent('manage_stu')"><span class="glyphicon glyphicon-user">&nbsp;<a class="border-bottom">学生管理</a></span></li>
      <li class="line"></li>
      <li class="border-bottom text-center"  v-on:click.prevent="changecomponent('manage_tea')"><span class="glyphicon glyphicon-th-list">&nbsp;<a class="border-bottom">教师管理</a></span></li>
      <li class="line"></li>
      <li class="border-bottom text-center"  v-on:click.prevent="changecomponent('manage_course')"><span class="glyphicon glyphicon-ok">&nbsp;<a class="border-bottom">课程管理</a></span></li>
      <li class="line"></li>
      <li class="border-bottom text-center"  v-on:click.prevent="changecomponent('')"></li>

    </ul>
  </div>
</template>

<script>
    export default {
        name: "admin_menu",
        methods:{
            changecomponent(componentname){
                //alert("点击："+componentname);
                this.$emit('getcomponent',componentname);
            }
        },

    }
</script>

<style scoped>
  .logo{
    font-size: 60px;

  }
  .line{
    border-bottom: 1px solid;
  }
  .border-bottom:hover{
    background-color: coral;
  }
  .border-bottom{
    padding-top: 10px;
    height:40px;

  }
  a{
    color:#fff;
    font-size: large;
  }
  #app{
    margin-left:10%;
    margin-right: 10%;
    background-color: darkorange;
    color:white;
  }


</style>




