<template>
  <div id="app">
    <br>
    <nav class="header navbar navbar-default" role="navigation">
      <div class="container-fluid">
        <div class="navbar-header">
          <a class="navbar-brand" href="#">课程信息查询</a>
        </div>
        <form class="navbar-form navbar-right" role="search">
          <div class="form-group">
            <input type="text" class="form-control" placeholder="Search">
          </div>
          <button type="submit" class="btn btn-default">查询</button>
        </form>
      </div>
    </nav>
    <div>
      <el-table
        :data="componentdata"
        border
        style="width: 90%">
        <el-table-column
          fixed
          prop="name"
          label="课程名"
          width="150">
        </el-table-column>
        <el-table-column
          prop="teacher_id"
          label="课程老师"
          width="120">
        </el-table-column>
        <el-table-column
          prop="stu_num"
          label="选课人数"
          width="120">
        </el-table-column>
        <el-table-column
          prop="specialty"
          label="专业"
          width="120">
        </el-table-column>
        <el-table-column
          prop="institute"
          label="院系"
          width="150">
        </el-table-column>
        <el-table-column
          prop="class_time"
          label="开课时间"
          width="120">
        </el-table-column>
        <el-table-column
          prop="classroom"
          label="开课教室"
          width="100">

        </el-table-column>
        <el-table-column
          prop="credit"
          label="学分"
          width="100">

        </el-table-column>

      </el-table>
    </div>
  </div>
</template>

<script>
export default {
    name:'course',
    data(){
        return {}
    },
    props:['componentdata']
}
</script>

<style scoped>
  .header{
    width:90%;
  }
</style>



