<template>
  <div id="app">
    <br>
    <nav class="header navbar navbar-default" role="navigation">
      <div class="container-fluid">
        <div class="navbar-header">
          <a class="navbar-brand" href="#">课程信息查询</a>
        </div>
        <form class="navbar-form navbar-right" role="search">
          <div class="form-group">
            <input type="text" class="form-control" placeholder="Search">
          </div>
          <button type="submit" class="btn btn-default">查询</button>
        </form>
      </div>
    </nav>
    <div id="courselist">
        <el-collapse v-model="activeName" accordion>
          <el-collapse-item v-for="item in componentdata" v-bind:title="item.name" @click.native="getstulist(item.id)" name="1">
            <el-table
              :data="stulist"
              style="width: 100%"
              max-height="250">
              <el-table-column
                fixed
                prop="id"
                label="id"
                width="150">
              </el-table-column>
              <el-table-column
                prop="course_id"
                label="课程id"
                width="120">
              </el-table-column>
              <el-table-column
                prop="stu_id"
                label="学生id"
                width="120">
              </el-table-column>
              <el-table-column
                prop="score"
                label="分数"
                width="120">
              </el-table-column>

              <el-table-column
                fixed="right"
                label="操作"
                width="120">
                <template slot-scope="scope">
                  <el-button
                    @click.native.prevent="updatescore(scope.$index, scope.row.id)"
                    type="text"
                    size="small">
                    修改分数
                  </el-button>
                </template>
              </el-table-column>
            </el-table>

          </el-collapse-item>

        </el-collapse>
    </div>
    <el-dialog
      title="请输入要更改的分数"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">
      <input type="text" v-model="form.score">
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doupdate">确 定</el-button>
  </span>
    </el-dialog>


  </div>
</template>

<script>
  import axios from "axios"
  export default{
      name:'course_grade',
      data(){
          return {
              activeName: '0',
              stulist:[],
              dialogVisible:false,
              form:{
                  sc_id:0,
                  score:0
              },


              index:0
          }
      },
      methods:{
          getstulist(cou_id){
              axios.get("/api/getCourseStudent",{params:{
                      cou_id:cou_id
                  }}).then(res => {
                      console.log("获取该课程学生成功"+res.data)
                      this.stulist=res.data
              }).catch(err => {
                  console.log("获取该课程学生失败"+err)
              })
          },
          updatescore(index,id){
              alert(id);
              this.dialogVisible=true;
              this.form.sc_id = id;
              this.index = index

          },
          doupdate(){
              console.log(this.form.sc_id+","+this.form.score)
              axios.get("/api/updateScore",{params:{
                  sc_id:this.form.sc_id,
                  score:this.form.score
                  }}).then(res => {
                      console.log("修改成功");
                      this.dialogVisible = false;
                      this.stulist[this.index].score = this.form.score
              })
          },
          handleClose(done) {
              this.$confirm('确认关闭？')
                  .then(_ => {
                      done();
                  })
                  .catch(_ => {});}
      },
      props:['componentdata']
  }
</script>

<style scoped>
  #app{
    height:100%
  }
  .header{
    width:90%;
  }
  #courselist{

    width:90%;
    height:90%;
    overflow: auto;
  }

</style>



