            <p>
              <b>姓名：&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{componentdata['username']}}</b>
              <el-divider></el-divider>
              <b>专业：&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{componentdata['zhuanye']}}</b>
              <el-divider></el-divider>
              <b>院系：&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{componentdata['yuanxi']}}</b>
              <el-divider></el-divider>


            </p>


        </el-main>
      </el-container>


    </el-card>

  </div>
</template>

<script>
export default {
    name: 'stu_message',
    props:['componentdata'],
    data(){
        return {}
    }


}
</script>

<style scoped>
  #app{
     margin-left: 10px;
  }
  #card{
    width:90%
  }
  #pic{

    width:30%;
    height:100%;

  }

  #content{
    padding:45px;

    width:100%;
    height:100%;
    font-size: 20px;

  }

</style>



