<template>
  <div id="app">
    <ul class="nav nav-pills nav-stacked">
      <li>&nbsp;</li>

      <li class="text-center ">
        <br><br>
        <span class="glyphicon glyphicon-fire logo"></span>
        <br>
        <br>

        <br>
        <br>
      </li>
      <li class="line"></li>
      <li class="border-bottom text-center"  v-on:click.prevent="changecomponent('tea_message')"><span class="glyphicon glyphicon-user">&nbsp;<a class="border-bottom">个人信息</a></span></li>
      <li class="line"></li>
      <li class="border-bottom text-center"  v-on:click.prevent="changecomponent('course')"><span class="glyphicon glyphicon-th-list">&nbsp;<a class="border-bottom">课程信息</a></span></li>
      <li class="line"></li>
      <li class="border-bottom text-center"  v-on:click.prevent="changecomponent('course_grade')"><span class="glyphicon glyphicon-ok">&nbsp;<a class="border-bottom">课程成绩</a></span></li>


    </ul>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    name:'tea_menu',
    methods:{
        changecomponent(message){
            this.$emit("getshowcomponent",message);
            if(message == 'course'){
                var tea_id=this.$store.state.username;
                console.log("当前登录教师id为："+tea_id);
                axios.get("/api/getTCourses",{params:{
                    tea_id:tea_id
                    }}).then(res => {
                        console.log("获取课程成功，"+res.data);
                        this.$emit("getcomponentdata",res.data);
                }).catch(err => {
                    console.log("获取课程失败，"+err)
                })
            }else if(message == 'course_grade'){
                var course_stu_list=[];
                var tea_id=this.$store.state.username;
                console.log("当前登录教师id为："+tea_id);
                axios.get("/api/getTCourses",{params:{
                        tea_id:tea_id
                    }}).then(res => {
                    this.$emit("getcomponentdata",res.data);
                }).catch(err => {
                    console.log("获取课程失败，"+err)
                })

            }
        }
    }
}
</script>

<style scoped>
  .logo{
    font-size: 60px;

  }
  .line{
    border-bottom: 1px solid;
  }
  .border-bottom:hover{
    background-color: lightgreen;
  }
  .border-bottom{
    padding-top: 10px;
    height:40px;

  }
  a{
    color:#fff;
    font-size: large;
  }
  #app{
    margin-left:10%;
    margin-right: 10%;
    background-color: forestgreen;
    color:white;
  }
</style>



