<template>

  <div id="app">
    <br>
    <nav class="header navbar navbar-default" role="navigation">
      <div class="container-fluid">
        <div class="navbar-header">
          <a class="navbar-brand" href="#">课程信息查询</a>
        </div>
        <form class="navbar-form navbar-right" role="search">
          <div class="form-group">
            <input type="text" class="form-control" v-model="keyword" placeholder="Search">
          </div>

        </form>
      </div>
    </nav>
    <div id="datatable" class="datatable">
      <el-table
        :data="searchlist"
        border
        height="100%"
        style="width: 100%">
        <el-table-column
          fixed
          prop="name"
          label="课程名"
          width="150">
        </el-table-column>
        <el-table-column
          prop="teacher_id"
          label="课程老师"
          width="120">
        </el-table-column>
        <el-table-column
          prop="stu_num"
          label="选课人数"
          width="120">
        </el-table-column>
        <el-table-column
          prop="specialty"
          label="专业"
          width="120">
        </el-table-column>
        <el-table-column
          prop="institute"
          label="院系"
          width="150">
        </el-table-column>
        <el-table-column
          prop="class_time"
          label="开课时间"
          width="120">
        </el-table-column>
        <el-table-column
          prop="classroom"
          label="开课教室"
          width="100">

        </el-table-column>
        <el-table-column
          prop="credit"
          label="学分"
          width="100">

        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="120">
          <template slot-scope="scope">

            <el-button
              @click.native.prevent="deleteSelectedCourse(scope.$index, scope.row.sc_id)"
              type="text"
              size="small"
            >
              退选
            </el-button>

          </template>
        </el-table-column>
      </el-table>


    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    name: "course_message",
    props:['componentdata','course_list'],
    data(){
        return {
            keyword:'',
            searchlist:[]
        }
    },
    methods:{
        deleteSelectedCourse(index,id){
            //alert("选课信息id："+id);
            axios.delete("/api/deleteSelectedCourse",{params:{
                sc_id:id
                }}).then((res) => {
                    alert("退选成功");
                    //this.componentdata.splice(index,1);
                    this.searchlist.splice(index,1)

            }).catch(err => {
                alert("推选失败"+err)
            })
        }
    },
    mounted() {
        //console.log(this.componentdata);
        this.searchlist=this.componentdata

    },
    watch:{
        keyword:function(now,before){
            if(now == ''){
                this.searchlist = this.componentdata;
                console.log("keyword为空");
            }else{
                this.searchlist=[];
                for(var item in this.componentdata){
                    if(this.componentdata[item].name.indexOf(now)!=-1){
                        this.searchlist.push(this.componentdata[item])
                    }
                }
            }
        }
    }
}
</script>

<style scoped>
  #app{
    height:100%;

  }
  .header{
    width:90%;
  }
  #datatable{

    width: 90%;
    height: 90%;
    overflow: auto;
  }


</style>



