<template>
  <div id="app">
    <br>
    <nav class="header navbar navbar-default" role="navigation">
      <div class="container-fluid">
        <div class="navbar-header">
          <a class="navbar-brand" href="#">课程信息查询</a>
        </div>
        <form class="navbar-form navbar-right" role="search">
          <div class="form-group">
            <input type="text" class="form-control" v-model="keyword" placeholder="请输入课程名">
          </div>

        </form>
      </div>
    </nav>
    <div id="datatable">
      <el-table
        :data="searchlist"
        border
        height="100%"
        style="width: 100%">
        <el-table-column
          fixed
          prop="name"
          label="课程名"
          width="150">
        </el-table-column>
        <el-table-column
          prop="teacher_id"
          label="课程老师"
          width="120">
        </el-table-column>
        <el-table-column
          prop="stu_num"
          label="选课人数"
          width="120">
        </el-table-column>
        <el-table-column
          prop="specialty"
          label="专业"
          width="120">
        </el-table-column>
        <el-table-column
          prop="institute"
          label="院系"
          width="150">
        </el-table-column>
        <el-table-column
          prop="class_time"
          label="开课时间"
          width="120">
        </el-table-column>
        <el-table-column
          prop="classroom"
          label="开课教室"
          width="100">

        </el-table-column>
        <el-table-column
          prop="credit"
          label="学分"
          width="100">

        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="120">
          <template slot-scope="scope">

            <el-button
              @click.native.prevent="selectCourse(scope.$index, scope.row.id)"
              type="text"
              size="small"
              v-if="course_list.indexOf(scope.row.id)==-1"
            >
              选课
            </el-button>
            <p  v-else>
              已选
            </p>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
export default {
    name: "select_course",
    data(){
        return {
            form:{
                course_id:'',
                stu_id:'',
                score:0,
                course_name:''
            },
            keyword:'',
            searchlist:['1','bob','2'],
            test:'helloworld'
        }
    },
    methods:{
        selectCourse(index,id){
            //alert(this.$store.state.username)
           // alert(this.componentdata[index].id+","+id);
            this.form.course_id=id;
            this.form.stu_id=this.$store.state.username;
            this.form.score=0;
            this.form.course_name=this.componentdata[index].name;
            axios.post("/api/selectCourse",this.form).then((res) => {
                    console.log("选课成功"+res.data);
                    if(res.data =='添课失败：选课人数已满'){
                        alert(res.data)
                    }else{
                    this.course_list.push(id);}
            }).catch(err => {
                console.log("选课失败"+err);
            })
        },
        showsearchlist(){

        }
    },
    props:['componentdata',"course_list"],
    created() {

        if(this.componentdata.length == 0){
            this.componentdata=[]
        }
        this.searchlist=this.componentdata
        // console.log(this.componentdata.length);
        // console.log(this.componentdata);
        // console.log("已选课程："+this.course_list);
        // console.log("finish")
    },
    watch:{
        keyword:function(now,old){
            console.log("keyword发生改变，"+old+"=>"+now);

            if(now == ""){
                this.searchlist=this.componentdata
                console.log("keyword==''")
            }else{
                this.searchlist=[]
                //console.log("keyword!=''"+this.test)
                for(var item in this.componentdata){
                    if(this.componentdata[item].name.indexOf(now) != -1){
                        this.searchlist.push(this.componentdata[item])
                    }

                }

            }
        }
    }

}
</script>


<style scoped>
  #app{
    height:100%
  }
  .header{
    width:90%;
  }
  #datatable{
    width: 90%;
    height:90%;
    overflow: auto;
  }
</style>



