<template>
  <div id="app">
    <br>
    <nav class="header navbar navbar-default" role="navigation">
      <div class="container-fluid">
        <div class="navbar-header">
          <a class="navbar-brand" href="#">课程信息管理</a>
        </div>



        <form class="navbar-form navbar-right" role="search">
          <button type="button" class="btn btn-default" @click="paike">一键排课</button>
          <button type="button" class="btn btn-default" @click="drawer = true"><span class="	glyphicon glyphicon-plus"></span></button>
          <div class="form-group">
            <input type="text" class="form-control" placeholder="Search">
          </div>
          <button type="submit" class="btn btn-default">查询</button>
        </form>
      </div>
    </nav>
    <!--添加课程信息-->
    <el-drawer
      title="添加课程信息"
      :visible.sync="drawer"
      :direction="direction"
    >
      <div style="width:90%">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="课程名">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="课程教师">
            <el-input v-model="form.teacher_id"></el-input>
          </el-form-item>

          <el-form-item label="学生人数">
            {{form.stu_num}}
          </el-form-item>
          <el-form-item label="专业">
            <el-input v-model="form.specialty"></el-input>
          </el-form-item>
          <el-form-item label="学院">
            <el-input v-model="form.institute"></el-input>
          </el-form-item>
          <el-form-item label="开课时间">
            <el-input v-model="form.class_time"></el-input>
          </el-form-item>
          <el-form-item label="开课教室">
            <el-input v-model="form.classroom"></el-input>
          </el-form-item>
          <el-form-item label="学分">
            <el-input v-model="form.credit"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">立即添加</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>

      </div>

    </el-drawer>
    <!--修改课程信息-->

    <el-drawer
      title="修改课程信息"
      :visible.sync="updatedrawer"
      :direction="direction"
    >
      <div style="width:90%">
        <el-form ref="updateform" :model="updateform" label-width="80px">

          <el-form-item label="课程名">
            <el-input v-model="updateform.name"></el-input>
          </el-form-item>
          <el-form-item label="课程教师">
            <el-input v-model="updateform.teacher_id"></el-input>
          </el-form-item>


          <el-form-item label="专业">
            <el-input v-model="updateform.specialty"></el-input>
          </el-form-item>
          <el-form-item label="学院">
            <el-input v-model="updateform.institute"></el-input>
          </el-form-item>
          <el-form-item label="开课时间">
            <el-input v-model="updateform.class_time"></el-input>
          </el-form-item>
          <el-form-item label="开课教室">
            <el-input v-model="updateform.classroom"></el-input>
          </el-form-item>
          <el-form-item label="学分">
            <el-input v-model="updateform.credit"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="updateCourse">立即修改</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>

      </div>

    </el-drawer>

    <div id="datatable">
      <el-table
        :data="course_list"
        border
        height="100%"
        style="width: 100%">
        <el-table-column
          fixed
          prop="name"
          label="课程名"
          width="150">
        </el-table-column>
        <el-table-column
          prop="teacher_id"
          label="课程教师"
          width="120">
        </el-table-column>
        <el-table-column
          prop="stu_num"
          label="学生人数"
          width="200">
        </el-table-column>
        <el-table-column
          prop="specialty"
          label="专业"
          width="200">
        </el-table-column>
        <el-table-column
          prop="institute"
          label="学院"
          width="200">
        </el-table-column>
        <el-table-column
          prop="class_time"
          label="开课时间"
          width="200">
        </el-table-column>
        <el-table-column
          prop="classroom"
          label="开课教室"
          width="200">
        </el-table-column>
        <el-table-column
          prop="credit"
          label="学分"
          width="200">
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="120"
        >
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="deleteRow(scope.$index, scope.row.id,course_list)"
              type="text"
              size="small">
              移除
            </el-button>
            <el-button
              @click.native.prevent="updateRow(scope.$index,scope.row.id)"
              type="text"
              size="small">
              修改
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name:'manage_course',
        data(){
            return {
                updatedrawer:false,
                drawer: false,
                update:'',//被修改的课程号
                updateIndex:'',//被修改课程所在行号
                direction: 'rtl',
                form: {
                    name:'',
                    teacher_id:'',
                    stu_num:0,
                    specialty:'',
                    institute:'',
                    class_time:'',
                    classroom:'',
                    credit:''
                },
                updateform:{
                    id:0,
                    name:'',
                    teacher_id:'',
                    stu_num:0,
                    specialty:'',
                    institute:'',
                    class_time:'',
                    classroom:'',
                    credit:''
                }
            }
        },
        methods:{
            onSubmit(){//添加课程信息
                axios.post('/api/addcourse',this.form).then((res) => {
                    console.log("添加课程信息成功，状态："+res.data.toString());
                    this.drawer=false;
                });
            },
            deleteRow(index,id,rows){
                axios.delete('/api/deleteCourse',{params:{
                    cou_id:id
                    }}).then((res) => {
                        console.log(id+"课程删除成功");
                        this.course_list.splice(index,1);
                }).catch(err => {
                    console.log("删除失败");
                })
            },
            updateRow(index,id){
                this.updateIndex=index;
                this.updatedrawer=true;
                this.update=id;
                this.updateform.name=this.course_list[index].name;
                this.updateform.teacher_id=this.course_list[index].teacher_id;
                //this.updateform.stu_num=this.course_list[index].
                this.updateform.specialty=this.course_list[index].specialty;
                this.updateform.institute=this.course_list[index].institute;
                this.updateform.class_time=this.course_list[index].class_time;
                this.updateform.classroom=this.course_list[index].classroom;
                this.updateform.credit=this.course_list[index].credit;
            },
            updateCourse(){
                axios.put('/api/updateCourse',this.updateform).then((res) => {
                    console.log("课程信息修改成功");
                    this.course_list[this.updateIndex]=this.updateform;
                    this.updatedrawer=false;
                }).catch(err => {
                    console.log("课程信息修改失败"+err);
                })
            },
            paike(){
                axios.get("/api/distributecourse").then(res => {
                    console.log(res.data)
                })
            }
        },
        props:['course_list'],
        created() {
            console.log(this.course_list.length)
        }
    }
</script>

<style scoped>
  #app{
    height:100%;
  }
  .header{
    width:90%
  }
  #datatable{
    width: 90%;
    height:90%;
    overflow: auto;
  }
</style>



