<template>
  <div id="app">

    <div id="father">
      <div class="stu_menu">
        <student_menu @getcourse_list="getcourse_list"  v-on:getshowcomponent="getshowcomponent"></student_menu>
      </div>
      <div class="content">
        <component v-bind:is="showcomponent" :course_list="course_list" :componentdata="componentdata"></component>
      </div>
    </div>
  </div>
</template>

<script>
  import student_menu from './components/menu';
  import stu_message from "./components/stu_message";
  import course_message from "./components/course_message";
  import select_course from "./components/select_course";
export default {
    name:'student',
    components:{
      student_menu,stu_message,course_message,select_course
    },
    data(){
        return {
            showcomponent:'stu_message',
            componentdata:[],
            course_list:[]
        }
    },
    methods:{
        getcourse_list(fromson){
            // console.log("获取到的子选课信息："+fromson);
            this.course_list=fromson;
        },
        change_showcomponent(showcomponent){
            this.showcomponent=showcomponent;
        },
        getshowcomponent(fromson,data){
            this.showcomponent=fromson;
            this.componentdata=data;
        }
    },
    watch:{
        showcomponent:(now,before)=>{
            console.log("showcomponent发生变化！"+before+"->"+now);
        },


    },
    mounted() {
        console.log("身份："+sessionStorage.getItem("status"))
        console.log("登录身份(session)："+sessionStorage.getItem('username'))
        if(sessionStorage.getItem('username') == null ||sessionStorage.getItem("status") != 'student'){
            console.log("未登录");
            this.$router.push('/login');
        }else{
            if(this.$store.state.username == ''){
              this.$store.dispatch('changeusername',sessionStorage.getItem('username'));
              console.log("同步登录")
        }
        }
    }
}
</script>

<style scoped>
  #father{
    height:100%;
  }
  .stu_menu{
    width: 17%;
    height: 100%;
    background-color: darkslateblue;
  }
  #father .content{
    background-color: #fff;
    width:75%;
    height:95%;
    position: absolute;
    left:25%;
    top:0px;
   }
</style>



