<template>
  <div id="app">
    <br>
    <nav class="header navbar navbar-default" role="navigation">
      <div class="container-fluid">
        <div class="navbar-header">
          <a class="navbar-brand" href="#">教师信息管理</a>
        </div>



        <form class="navbar-form navbar-right" role="search">
          <button type="button" class="btn btn-default" @click="drawer = true"><span class="glyphicon glyphicon-plus"></span></button>
          <div class="form-group">
            <input type="text" class="form-control" placeholder="Search">
          </div>
          <button type="submit" class="btn btn-default">查询</button>
        </form>
      </div>
    </nav>
    <el-drawer
      title="添加教师信息"
      :visible.sync="drawer"
      :direction="direction"
    >
      <div style="width:90%">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="教师id">
            <el-input v-model="form.id"></el-input>
          </el-form-item>
          <el-form-item label="教师姓名">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="登录密码">
            <el-input v-model="form.password"></el-input>
          </el-form-item>


          <el-form-item label="学院">
            <el-input v-model="form.institute"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="onSubmit">立即添加</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>

      </div>
    </el-drawer>

    <el-drawer
      title="修改教师信息"
      :visible.sync="updatedrawer"
      :direction="direction"
    >
      <div style="width:90%">
        <el-form ref="updateform" :model="updateform" label-width="80px">
          <el-form-item label="教师id">
            {{updateform.id}}
          </el-form-item>
          <el-form-item label="教师姓名">
            <el-input v-model="updateform.name"></el-input>
          </el-form-item>
          <el-form-item label="登录密码">
            <el-input v-model="updateform.password"></el-input>
          </el-form-item>


          <el-form-item label="学院">
            <el-input v-model="updateform.institute"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="updateTeacher">立即修改</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>

      </div>
    </el-drawer>
    <div id="datatable">
      <el-table
        :data="list"
        border
        height="100%"
        style="width: 100%">
        <el-table-column
          fixed
          prop="name"
          label="教师姓名"
          width="200">
        </el-table-column>
        <el-table-column
          prop="password"
          label="登录密码"
          width="200">
        </el-table-column>

        <el-table-column
          prop="institute"
          label="学院"
          width="200">
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="200"
        >
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="deleteRow(scope.$index, scope.row.id,tea_list)"
              type="text"
              size="small">
              移除
            </el-button>
            <el-button
              @click.native.prevent="updateRow(scope.$index,scope.row.id,tea_list)"
              type="text"
              size="small">
              修改
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import axios from  'axios'
    export default {
        name:'manage_tea',
        data(){
            return {
                list:[],
                updateindex:'',
                updatedrawer:false,
                drawer: false,
                direction: 'rtl',
                form: {
                    id:'',
                    name: '',
                    password: '',
                    institute: '',

                },
                updateform:{
                    id:"",
                    name:'',
                    password:'',
                    institute:''

                }
            }
        },
        methods:{
            onSubmit(){//添加教师信息
                axios.post('/api/addTeacher',this.form).then((res) => {
                    console.log("添加教师信息成功，状态："+res.data.toString());
                    this.drawer=false;
                    this.tea_list.splice(0,0,{id:this.form.id,name:this.form.name,password:this.form.password,institute:this.form.institute});
                });
            },
            deleteRow(index,id,rows){
                //alert("要删除的是第"+index+"行，id="+id+"的用户");
                axios.delete('/api/deleteTeacher',{params:{
                        tea_id:id
                    }}).then((res) => {
                        console.log("教师信息删除成功");
                }).catch(err => {
                    console.log(err)
                });
                rows.splice(index,1);
            },
            updateRow(index,id,rows){
                this.updateindex=index;
                this.updatedrawer = true;
                this.updateform.id = id;
                this.updateform.name=rows[index].name;
                this.updateform.password=rows[index].password;
                this.updateform.institute=rows[index].institute;
            },
            updateTeacher(){
                axios.put("/api/updateTeacher",this.updateform).then((res) => {
                    console.log("更改成功");
                    this.tea_list[this.updateindex].name=this.updateform.name;
                    this.tea_list[this.updateindex].password=this.updateform.password;
                    this.tea_list[this.updateindex].institute=this.updateform.institute;
                    this.updatedrawer=false;
                }).catch(err => {
                    console.log(err);
                })
            }
        },
        props:['tea_list'],
        created() {
            if(this.tea_list.name != 'null'){
                //alert("获取到了tea_list");
                this.list = this.tea_list;
            }
        }
    }
</script>

<style scoped>
  .header{
    width:90%
  }
  #datatable{
    width: 90%;
    height:90%;
    overflow: auto;
  }
</style>



