<template>

  <div id="app">




    <el-container>
      <el-header>

      </el-header>
      <el-main class="text-center">
        <br><br>
        <!-- 模态框（Modal） -->
        <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-body">


                <div id="loginform">
                  <form class="form-horizontal"  role="form">
                    <div class="form-group">
                      <label for="firstname" class="col-sm-2 control-label">名字</label>
                      <div class="col-sm-10">
                        <input type="text" class="form-control" v-model="form.id" id="firstname" placeholder="请输入id">
                      </div>
                    </div>
                    <div class="form-group">
                      <label for="lastname" class="col-sm-2 control-label">密码</label>
                      <div class="col-sm-10">
                        <input type="password" class="form-control" id="lastname" v-model="form.password" placeholder="请输入密码">
                      </div>
                    </div>

                    <div class="form-group">
                      <label for="lastname" class="col-sm-2 control-label">选择要登录的身份</label>
                      <div class="radio">
                        <label class="radio-inline">
                          <input type="radio"  name="status" v-model="form.status" value="student">学生
                        </label>
                        <label class="radio-inline">
                          <input type="radio"  name="status" v-model="form.status" value="teacher">老师
                        </label>
                        <label class="radio-inline">
                          <input type="radio"  name="status" v-model="form.status" value="admin">管理员
                        </label>
                      </div>
                    </div>

                    <div class="form-group">
                      <div class="col-sm-offset-2 col-sm-10">
                        <div class="checkbox">
                          <label>
                            <input type="checkbox">请记住我
                          </label>
                        </div>
                      </div>
                    </div>
                    <div class="form-group">
                      <div class="col-sm-offset-2 col-sm-10">

                      </div>
                    </div>
                  </form>
                </div>


              </div>

              <div class="modal-footer">
                <button type="button" class="btn btn-default" data-dismiss="modal" @click="postmessage">登录</button>

              </div>
            </div><!-- /.modal-content -->
          </div><!-- /.modal -->
        </div>

        <span class="glyphicon glyphicon-fire logo"></span>
        <br><br><br><br>
        <p class="word">纯手工打造，1107教务管理系统，让教务更简单</p>
        <br>

        <button class="loginbtn btn btn-primary btn-lg" data-toggle="modal" data-target="#myModal">登录 1107教务管理系统</button>
      </el-main>
      <el-footer></el-footer>
    </el-container>



  </div>
</template>

<script>
import axios from 'axios'

export default{

    name:'login',
    data(){
        return {
            form:{
            usr_name:'',
            id: '',
            password: '',
            status:''}
        }
    },
    methods:{
        postmessage(username,password){
            var url='';
            if(this.form.status == 'student'){
                url="/api/studentSignIn";
            }else if(this.form.status == 'teacher'){
                url="/api/teacherSignIn";
            }else if(this.form.status == 'admin'){
                url="/api/adminSignIn";
            }else{
                alert("请选择登陆类型");
                return true;
            }
            axios.get(url,{params:{
                id:this.form.id,
                usr_name:this.form.id,
                tea_id:this.form.id,
                password:this.form.password
                }}).then(res => {
                console.log("状态："+res.data.toString());
                console.log("未存储："+sessionStorage.getItem('username'));

               if(res.data.toString() == "登陆成功"){
                   console.log('登录成功！');
                   sessionStorage.setItem('username',this.form.id);
                   sessionStorage.setItem('status',this.form.status);
                   this.$store.dispatch('changeusername',this.form.id);
                   this.$store.dispatch('changestatus',this.form.status);
                   if(this.form.status == 'student'){
                       this.$router.push('/student');
                   }else if(this.form.status == 'teacher'){
                       this.$router.push('/teacher');
                   }else if(this.form.status == 'admin'){
                       this.$router.push('/admin');
                   }else{
                       console.log(this.status)
                   }

               } else {
                   console.log('用户明或密码错误，请重试');
                   alert(res.data.toString());
                   this.username = '';
                   this.password = '';
               }

            }).catch(err => {
                console.log("请求发送失败，"+err)
            })
        }
    }


}
</script>

<style scoped>
 #loginform{
   width: 100%;
   height:100%;


 }

  #app{
    width: 100%;
    height:100%;
    background: url("../pic/bg_orangeandblue.jpeg") no-repeat;
    background-size: cover;

  }
  .logo{

    font-size: 150px;
    color:white;

  }
  .word{
    color:white;
    font-size:25px;
  }
  .loginbtn{
    background:transparent;
    border:1px solid white;
  }


</style>



