import Vue from 'vue'
import Router from 'vue-router'
import login from '@/pages/login'
import student from '@/pages/student'
import welcome from '@/pages/welcome'
import teacher from '@/pages/teacher'
import admin from '@/pages/admin'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'welcome',
      component: welcome
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/student',
      name: 'student',
      component: student
    },
    {
      path: '/teacher',
      name: 'teacher',
      component: teacher
    },
    {
      path: '/admin',
      name: 'admin',
      component: admin
    }

  ]
})



