<template>
  <div id="app">
    <div id="father">
      <div class="menu">
        <admin_menu v-on:getcomponent="getcomponent" ></admin_menu>
      </div>
      <div class="content">
        <component v-bind:is="showcomponent" :course_list="course_list" :tea_list="tea_list"  :stu_list = "stu_list"></component>
      </div>
    </div>

  </div>
</template>


<script>
import axios from 'axios'
import admin_menu from "./components/menu"
import manage_stu from "./components/manage_stu";
import manage_tea from "./components/manage_tea";
import manage_course from "./components/manage_course";
export default {
    name:'admin',
    data(){
        return {
            showcomponent:'',
            stu_list:{},
            tea_list:{},
            course_list:[]


        }
    },
    components:{
        manage_tea,manage_course,manage_stu,admin_menu
    },
    methods:{
        getcomponent(fromson){
            this.showcomponent = fromson;
        }
    },
    created(){
        axios.get("/api/getStudent").then((res) => {
            console.log("获取学生列表成功"+res.data);
            this.stu_list = res.data;

        }).catch(err => {
            console.log("获取学生列表失败");
            this.stu_list={id:'null',
                        name:'null',
                        password:'null',
                        specialty:'null',
                        institute:'null'};


        });

        axios.get("/api/getTeacher").then((res) => {
            console.log("获取老师列表成功"+res.data);
            this.tea_list = res.data;
        }).catch(err => {
            console.log("获取老师列表失败");
            this.tea_list={id:'null',
                name:'null',
                password:'null',
                specialty:'null',
                institute:'null'};

        });

        axios.get('/api/getCourse').then((res) => {
            console.log(("获取课程信息列表成功"+res.data));
            this.course_list = res.data

        }).catch(err => {
            console.log("获取课程列表失败");
            this.course_list=[]
        })
    },
    mounted() {
        console.log("身份："+sessionStorage.getItem("status"))

        console.log("登录身份(session)："+sessionStorage.getItem('username'))
        if(sessionStorage.getItem('username') == null ||sessionStorage.getItem("status") != 'admin'){
            console.log("未登录");
            this.$router.push('/login');
        }else{

            if(this.$store.state.username == ''){
                this.$store.dispatch('changeusername',sessionStorage.getItem('username'));
                console.log("同步登录")
            }
        }
    }
}

</script>

<style scoped>
  #father{
    height:100%;
  }
  .menu{
    width: 17%;
    height: 100%;
    background-color: darkorange;
  }
  #father .content{


    background-color: #fff;
    width:75%;
    height:95%;
    position: absolute;
    left:25%;
    top:0px;
  }

</style>



