<template>
  <div id="app">
    <div id="father">
      <div class="tea_meanu">
        <tea_menu @getcourse_stu_list="getcourse_stu_list" v-on:getshowcomponent="getshowcomponent" @getcomponentdata="getcomponentdata"></tea_menu>
      </div>

      <div id="content" class="content">
        <component :componentdata="componentdata" v-bind:is="showcomponent"></component>
      </div>
    </div>


  </div>
</template>

<script>
import tea_menu from './component/menu'
import tea_message from "./component/tea_message"
import course from "./component/course"
import course_grade from "./component/course_grade";
export default {
  name:'teacher',
  data(){
      return {
          showcomponent:'',
          componentdata:[],
          course_stu_list:[]
      }
  },
  components:{
      tea_menu,tea_message,course,course_grade
  },
  methods:{
      getshowcomponent(fromson){
          this.showcomponent=fromson;
      },
      getcomponentdata(fromson){
          this.componentdata=fromson;
          console.log("获取到componentdata"+fromson)
      },
      getcourse_stu_list(fromson){
          this.componentdata=fromson;
          console.log("course_stu_list:"+fromson)
      }
  },
  watch:{
      showcomponent:(now,old) => {
          console.log("检测到发生showcomponent变化"+old+"=>"+now)
      }
  },
  mounted() {
      console.log("身份："+sessionStorage.getItem("status"))

      console.log("登录身份(session)："+sessionStorage.getItem('username'))
      if(sessionStorage.getItem('username') == null ||sessionStorage.getItem("status") != 'teacher'){
          console.log("未登录");
          this.$router.push('/login');
      }else{

          if(this.$store.state.username == ''){
              this.$store.dispatch('changeusername',sessionStorage.getItem('username'));
              console.log("同步登录")
          }
      }
  }

}
</script>

<style scoped>
  #father{
    height:100%;

  }
  .tea_meanu{
    width: 17%;
    height: 100%;
    background-color: forestgreen;
  }
  #father .content{

    background-color: #fff;
    width:75%;
    height:95%;
    position: absolute;
    left:25%;
    top:0px;
  }
</style>



