<template>
  <div id="app">
    <br>
    <nav class="header navbar navbar-default" role="navigation">
      <div class="container-fluid">
        <div class="navbar-header">
          <a class="navbar-brand" href="#">学生信息管理</a>
        </div>



        <form class="navbar-form navbar-right" role="search">
          <button type="button" class="btn btn-default" @click="drawer = true"><span class="glyphicon glyphicon-plus"></span></button>
          <div class="form-group">
            <input type="text" class="form-control" placeholder="Search">
          </div>
          <button type="submit" class="btn btn-default">查询</button>
        </form>
      </div>
    </nav>
<!--这是添加学生的drawer-->
    <el-drawer
      title="添加学生信息"
      :visible.sync="drawer"
      :direction="direction"
      >
      <div style="width:90%">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="学生ID">
            <el-input v-model="form.id"></el-input>
          </el-form-item>
          <el-form-item label="学生姓名">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="登录密码">
            <el-input v-model="form.password"></el-input>
          </el-form-item>

          <el-form-item label="专业">
            <el-input v-model="form.specialty"></el-input>
          </el-form-item>
          <el-form-item label="学院">
            <el-input v-model="form.institute"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="onSubmit(stu_list)">立即添加</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>

      </div>




    </el-drawer>
<!--这是更改学生信息的drawer-->
    <el-drawer
      title="更改学生信息"
      :visible.sync="drawer_update"
      :direction="direction"
    >
      <div style="width:90%">
        <el-form ref="form" :model="updateform" label-width="80px">
          <el-form-item label="学生ID">
            {{update}}
          </el-form-item>
          <el-form-item label="学生姓名">
            <el-input v-model="updateform.name"></el-input>
          </el-form-item>
          <el-form-item label="登录密码">
            <el-input v-model="updateform.password"></el-input>
          </el-form-item>

          <el-form-item label="专业">
            <el-input v-model="updateform.specialty"></el-input>
          </el-form-item>
          <el-form-item label="学院">
            <el-input v-model="updateform.institute"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="updateStudent(updateindex,update,stu_list)">更改</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>

      </div>




    </el-drawer>
<!--stu_list-->
    <div id="datatable">
      <el-table
        :data="list"
        border
        height="100%"
        style="width: 100%">

        <el-table-column
          fixed
          prop="name"
          label="学生姓名"
          width="150">
        </el-table-column>
        <el-table-column
          prop="password"
          label="登录密码"
          width="120">
        </el-table-column>
        <el-table-column
          prop="specialty"
          label="专业"
          width="200">
        </el-table-column>
        <el-table-column
          prop="institute"
          label="学院"
          width="200">
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="120"
        >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="deleteRow(scope.$index, scope.row.id,stu_list)"
            type="text"
            size="small">
            移除
          </el-button>
          <el-button
            @click.native.prevent="updateRow(scope.$index, scope.row.id,stu_list,scope.row)"
            type="text"
            size="small">
            修改
          </el-button>
        </template>
        </el-table-column>
      </el-table>
    </div>

  </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name:'manage_stu',
        data(){
           return {
               list:[],
               update:'',
               updateindex:'',
               drawer: false,
               drawer_update:false,
               direction: 'rtl',
               form: {
                   id:'',
                   name: '',
                   password: '',
                   specialty: '',
                   institute: '',

               },
               updateform:{
                   id:'',
                   name: '',
                   password: '',
                   specialty: '',
                   institute: '',
               }
           }
        },
        methods:{
            onSubmit(rows){//添加学生信息

                // axios.get("http://localhost:8088/addStudent2",{params:{
                //         id:this.form.id,
                //         name:this.form.name,
                //         password:this.form.password,
                //         specialty:this.form.specialty,
                //         institute:this.form.institute
                //     }}).then((res) => {
                //         console.log("get方法添加学生信息成功");
                // });

                axios.post('/api/addStudent',this.form).then((res) => {
                    console.log("添加学生信息成功，状态："+res.data.toString());
                    this.drawer=false;
                    rows.splice(0,0,{name:this.form.name,password:this.form.password,specialty:this.form.specialty,institute:this.form.institute});

                });
            },
            deleteRow(index,id,rows){
                axios.delete('/api/deleteStudent',{params:{
                    stu_id:id
                    }}).then((res) => {
                        console.log("删除成功")
                });
                rows.splice(index,1);


            },
            updateRow(index,id,rows,row){
                this.updateindex=index;
                this.drawer_update=true;
                this.update=id;
                this.updateform.id=id;
                this.updateform.name=row.name;
                this.updateform.institute=row.institute;
                this.updateform.password=row.password;
                this.updateform.specialty=row.specialty;
            },
            updateStudent(index,id,rows){
                axios.put("/api/updateStudent",this.updateform).then((res) => {
                    console.log("修改成功");
                });
                rows[index].name=this.updateform.name;
                rows[index].password=this.updateform.password;
                rows[index].specialty=this.updateform.specialty;
                rows[index].institute=this.updateform.institute;
                //alert("要更改的学生学号为"+id);
                //alert("要更改的学生姓名"+rows[index].name);
                this.drawer_update=false;

            }
        },
        props:['stu_list'],
        created() {
            if(this.stu_list.name != 'null'){
                //alert("获取到stu_list");
                this.list=this.stu_list
            }
        }

    }
</script>

<style scoped>
.header{
  width:90%
}
#datatable{
  width: 90%;
  height:90%;
  overflow: auto;
}
</style>



